@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
    min-height: 100vh;
    margin: 0;
    font-family: 'Raleway', sans-serif !important;
  
}
.header-container{
    display: flex;
    justify-content: space-between;
    margin: 0rem;
    padding: 0rem;
}

.header-menuIcon{
    color: white;
}

.signin-container{
    border: 1px solid black;
}

.signin-form{
    display: flex;
    flex-flow: column;
    align-items: center;
}

.sign-title{
    background-color: #416EEE;
    color: white;
    height: 6rem;
    width: 18rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.signin-member{
    margin-top: 2rem !important;
}
.overview-container {
    background-color: #416EEE;
    padding: 1rem;
    margin: 0;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
    box-shadow: 0px 4px 4px 0px #00000040;
    overflow-y: scroll;
}

.overview-historyText {
    color: #D1d1d1;
    font-size: .72rem;
}
.header-container{
    display: flex;
    justify-content: space-between;
    margin: 0rem;
    padding: 0rem;
    float: right;
}

.header-menuIcon{
    color: white;
}
.navbar-container{
    display: flex;
    justify-content: space-evenly;
    border-top: .1rem solid whitesmoke;
    padding: 1rem;
    align-items: center;
    margin-top: 2rem;
}

.navbar-icon{
    color: lightgray;
}

.active{
    color: #416EEE;
}
.balance-text{
    color: white;
    font-weight: 300;
    color: whitesmoke;
    font-size: .9rem;
}

.balance-amunt{
    display: flex;
    align-items: center;
    color: white;
    font-size: 1.7rem;
}

.balance-icon{
    font-size: medium;
}
.popUp-wrap {
    margin: auto !important;
    padding: 0 !important;
    top: 0 !important;
    left: 0 !important;
    z-index: 2 !important;
    display: block !important;
    width: 100% !important;
    height: 100% !important;
    position: fixed !important;
    background-color: rgba(0, 0, 0, 0.137) !important;
  }
  
  .popUp-wrap .popUp {
    margin: 80px auto;
    width: 20rem;
    position: relative;
    background-color: white;
    border-radius: 1rem;
    box-shadow: 3px 3px 10px gray;
  }
  
  .popUp-wrap .popUp img {
    height: 500px;
    width: 500px;
    position: relative;
  }
  
  .popUp-wrap .popUp .popUp-btn {
    position: absolute;
    right: 0;
    padding-top: 0.5rem;
    padding-right: 0.9rem;
    color: lightgray;
  }

  .popUp-btn:hover{
    cursor: pointer;
    color: black !important;
  }
  
  .popUp-newPost {
    text-align: center;
    font-size: 1.5rem;
    padding-top: 1.5rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid darkgray;
  }
  
  .popUp-container {
    display: flex;
    justify-content: center;
    grid-gap: 10rem;
    gap: 10rem;
    margin: .5rem 0rem;
  }

  .popUp-inputContainer{
    margin: 1rem;
  }

  .popUp-btnContainer{
    display: flex;
    justify-content: space-evenly;
    margin: 2rem 0rem;
  }
  
  .popUp-btnUpdate{
    background-color: #416EEE;
    color: white;
    border-radius: .5rem;
  }

  .delete{
    background-color: #BA382E !important;
  }

  .popUp-text{
    margin-bottom: .5rem;
  }
.comingUp-text {
    font-size: 1rem;
    color: #676767 !important;
}

.comingUp-header {
    display: flex;
    justify-content: space-between;
}

.comingUp-details {
    font-size: small;
    color: lightgray;
}

.comingUp-details:hover {
    cursor: pointer;
    transform: scale(1.2);
}

.comingUp-container {
    padding: 1rem;
    margin: 0;
    width: 20.45rem;
    display: inline-table;
    justify-content: center;
}

.comingUp-cardContainer {
    display: -webkit-inline-box;
    max-width: 20.45rem;
    max-height: 9rem;
    overflow-x: scroll;
}

.comingUp-card {
    width: 128px;
    box-shadow: 0px 2px 4px 0px #00000040;
    border-radius: .8rem;
    margin: .5rem;
    padding: .5rem;
}

.comingUp-positive {
    border: .1rem solid #31C56E;

}

.comingUp-negative {
    border: .1rem solid #BA382E;
}

.comingUp-itemName {
    font-size: 1.2rem;

}

.comingUp-date {
    font-size: .8rem;
    color: #676767;
    margin: .1rem 0rem;
}

.comingUp-amount {
    font-size: 1.2rem;
}
.comingUp-noExpense{
    color: lightgray;
}

/* RESPONSIVE DESIGN */

@media only screen and (min-width: 540px) {
    .comingUp-cardContainer {
        max-width: 31rem !important;
    }
}

@media only screen and (min-width: 768px) {
    .comingUp-cardContainer {
        max-width: 43.5rem !important;
    }
}

@media only screen and (min-width: 886px) {
    .comingUp-cardContainer {
        max-width: 51rem !important;
    }
}

@media only screen and (min-width: 1024px) {
    .comingUp-cardContainer {
        max-width: 57rem !important;
    }
}

@media only screen and (min-width: 1097px) {
    .comingUp-cardContainer {
        max-width: 61rem !important;
    }
}

@media only screen and (min-width: 1200px) {
    .comingUp-cardContainer {
        max-width: 68rem !important;
    }
}

@media only screen and (min-width: 1280px) {
    .comingUp-cardContainer {
        max-width: 72.5rem !important;
    }
}

@media only screen and (min-width: 1364px) {
    .comingUp-cardContainer {
        max-width: 78rem !important;
    }
}

@media only screen and (min-width: 1364px) {
    .comingUp-cardContainer {
        max-width: 86rem !important;
    }
}

@media only screen and (min-width: 1618px) {
    .comingUp-cardContainer {
        max-width: 95rem !important;
    }
}

@media only screen and (min-width: 1850px) {
    .comingUp-cardContainer {
        max-width: 109rem !important;
    }
}
.overview-historyContainer {
    justify-content: center;
    align-items: center;
    width: 100% !important;
    margin: 0 !important;
    grid-gap: 1rem;
    gap: 1rem;
    height: 15rem;
    overflow-y: scroll;
}

.overview-transaction {
    padding: .5rem !important;
    border-radius: .8rem;
    box-shadow: 0px 4px 4px 0px #00000040;
}

.css-1xgjpwf-MuiGrid-root {
    width: 100%;
    margin-left: 0rem;
}

.positive {
    background-color: #31C56E;
}

.negative {
    background-color: #BA382E;
}

.overview-list {
    display: grid;
    grid-template-columns: 33% 33% 10%;
    grid-gap: 2.5rem;
    gap: 2.5rem;
    color: #f3f3f3;
    padding: 1rem;
    align-items: center;
    justify-content: center;
}

.overview-text {
    font-size: .9rem;
}

.overview-amount {
    font-size: 1.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.detailsIcon {
    color: whitesmoke;
    opacity: 85%;
    font-size: small;
    align-items: center;
    justify-content: center;
}

.detailsIcon:hover {
    cursor: pointer;
    transform: scale(1.2);
}

.history-noExpense{
    display: flex;
    flex-flow: column;
    align-items: center;
    color: white;
}

.history-link{
    color: white;
}
